@import 'colors';
@import 'mixins';

.app__container {
  display: flex;
  flex-flow: row nowrap;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  width: 95vw;
  height: 95vh;
  margin: 0;
  padding: 0;
  align-items: center;
  justify-content: space-evenly;


  &-nav {
    justify-self: flex-start;
    width: 15%;
    height: 100%;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    background: $app-background;
    border-radius: 1rem;
    box-shadow: 1rem 1rem 2rem .7rem rgba(0, 0, 0, 0.25);
    -webkit-border-radius: 1rem;
    -moz-border-radius: 1rem;
    -ms-border-radius: 1rem;
    -o-border-radius: 1rem;

    @include respond(hide-nav) {
      display: none;
    }
  }

  & main {
    width: 80%;
    height: 100%;
    background: $app-background;
    border-radius: 1rem;
    -webkit-border-radius: 1rem;
    -moz-border-radius: 1rem;
    -ms-border-radius: 1rem;
    -o-border-radius: 1rem;
    color: $light-gray;
    box-shadow: 1rem 1rem 2.5rem 1.5rem rgba(0, 0, 0, 0.25);
    overflow: hidden;

    @include respond(hide-nav) {
      width: 100%;
    }

    @include respond (phone) {
      & {
        height: 88vh;
        margin-top: 3rem;
        margin-bottom: 3rem;
        padding-top: 3rem;
        padding-bottom: 3rem;
        overflow-y: scroll;
        scrollbar-width: none;
        -ms-overflow-style: none;

        &::-webkit-scrollbar {
          display: none;
        }
      }
    }

    @include respond (small-phone) {
      & {
        margin-top: 10rem;
      }
    }
  }

  &-main {
    width: 100%;
    height: 100%;
    text-align: center;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;

    @include respond (phone) {
    
      padding-top: 3rem;
    }
  }
}
