@import '../../colors';
@import '../../mixins';

.home__container {
    width: 100%;
    height: 90vh;
    position: relative;
    z-index: 3;

    &::before {
        content: "";
        background: linear-gradient(55deg, rgb(64, 63, 80) 0%, rgb(64, 63, 80) 45%, rgba(255, 255, 255, 0) 100%);
        background-size: cover;
        opacity: 0.7;
        top: -15%;
        left: 0;
        bottom: -5%;
        right: 0;
        position: absolute;
        z-index: 2;
    }

    &::after {
        content: "";
        background: url(../../assets/images/light-bulb.webp);
        background-size: cover;
        background-position-x: 40%;
        opacity: 0.4;
        top: -15%;
        left: 0;
        bottom: -5%;
        right: 0;
        position: absolute;
        z-index: 1;

        @include respond (large-tab-portrait) {
            & {
                background-position-x: 55%;
            }
        }

        @include respond (ipad) {
            & {
                background-position-x: 55%;
            }
        }

        @include respond (phone) {
            & {
              background-position-x: 60%;
            }
        }
    }

    .first-name__container {
        position: absolute;
        top: 8.8%;
        left: 5%;
        color: $app-boxes;
        z-index: 9;

        @include respond (large-tab-portrait) {
            & {
                top: 20%;
            }
        }

        @include respond (ipad) {
            & {
                top: 15%;
            }
        }

        @include respond (phone) {
            & {
                top: unset;
                height: 11rem;
                width: fit-content;
                bottom: 72%;
            }
        }

        & h1.first-name {
            position: absolute;
            top: 0;
            left: 0;
            -webkit-text-stroke: 2px $light-gray;
            opacity: 0.6;
            position: absolute;
            animation-name: opacity1;
            animation-duration: 1.5s;
            animation-timing-function: ease-out;

            @keyframes opacity1 {
                from {
                    opacity: 0;
                    transform: scale(1.3);
                    -webkit-transform: scale(1.3);
                    -moz-transform: scale(1.3);
                    -ms-transform: scale(1.3);
                    -o-transform: scale(1.3);
                }
                to {
                    opacity: 0.6;
                }
            }

        }
    }
    
    & h1 {
        font-size: 15rem;
        line-height: 15rem;
        margin: 0;
        padding: 0;
        font-family: 'Roboto Mono', monospace;

        @include respond (phone) {
            & {
                font-size: 10rem;
                line-height: 10rem;
            }
        }
    }

    & .second-column {
        position:  absolute;
        top: 20%;
        left: 20%;
        width: fit-content;
        height: 80%;
        margin: 0;
        padding: 0;
        display: flex;
        flex-flow: column nowrap;
        align-items: flex-start;
        justify-content: flex-start;
        z-index: 10;
        backface-visibility: hidden;
        -webkit-backface-visibility: hidden;

        @include respond (large-tab-portrait) {
            & {
                top: 27%;
                left: 15%;
            }
        }

        @include respond (ipad) {
            & {
                top: 23%;
                left: 15%;
            }
        }

        @include respond (phone) {
            & {
                top: 22.5%;
                left: 10%;
            }
        }

        & h1.last-name {
            color: $light-gray;
            animation-name: opacity2;
            animation-duration: 1.5s;
            animation-timing-function: ease-out;
            
            @keyframes opacity2 {
                from {
                    opacity: 0;
                    transform: scale(0.6);
                    -webkit-transform: scale(0.6);
                    -moz-transform: scale(0.6);
                    -ms-transform: scale(0.6);
                    -o-transform: scale(0.6);
                }
                to {
                    opacity: 1;
                }
            }

        }

        & h2 {
            margin: 0;
            padding: 0;
            animation: opacity3;
            -webkit-animation: opacity3;
            animation-duration: 3s;
            animation-timing-function: ease-out;

            @keyframes opacity3 {
                0% {
                    opacity: 0;
                }
                66% {
                    opacity: 0;
                }
                100% {
                    opacity: 1;
                }
            }
            @include respond (phone) {
                & {
                    font-size: 1.8rem;
                }
            }

        }

        & h2.web-dev {
            font-size: 3rem;
            transform: translate(.8rem, -1rem);
            -webkit-transform: translate(.8rem, -1rem);
            -moz-transform: translate(.8rem, -1rem);
            -ms-transform: translate(.8rem, -1rem);
            -o-transform: translate(.8rem, -1rem);
            -webkit-backface-visibility: hidden;
            -moz-backface-visibility: hidden;
            -ms-backface-visibility: hidden;
            animation-name: opacity4;
            animation-duration: 1.5s;

            @include respond (phone) {
                & {
                    font-size: 2.5rem;
                    transform: translate(.5rem, -1rem);
                    -webkit-transform: translate(.5rem, -1rem);
                    -moz-transform: translate(.5rem, -1rem);
                    -ms-transform: translate(.5rem, -1rem);
                    -o-transform: translate(.5rem, -1rem);
                }
            }

            @keyframes opacity4 {
                from {
                    opacity: 0;
                    transform: scale(1.2) translateZ(0);
                    -webkit-transform: scale(1.2) translateZ(0);
                    -moz-transform: scale(1.2) translateZ(0);
                    -ms-transform: scale(1.2) translateZ(0);
                    -o-transform: scale(1.2) translateZ(0);
                }
                to {
                    opacity: 1;
                }
            }
        }
    
        & h2.slogan-1 {
            transform: translate(.5rem, 12.5rem);
            -webkit-transform: translate(.5rem, 12.5rem);
            -moz-transform: translate(.5rem, 12.5rem);
            -ms-transform: translate(.5rem, 12.5rem);
            -o-transform: translate(.5rem, 12.5rem);

            @include respond (large-tab-portrait) {
                & {
                    transform: translate(.5rem, 20rem);
                    -webkit-transform: translate(.5rem, 20rem);
                    -moz-transform: translate(.5rem, 20rem);
                    -ms-transform: translate(.5rem, 20rem);
                    -o-transform: translate(.5rem, 20rem);
                }
            }

            @include respond (ipad) {
                & {
                    transform: translate(.5rem, 25rem);
                    -webkit-transform: translate(.5rem, 25rem);
                    -moz-transform: translate(.5rem, 25rem);
                    -ms-transform: translate(.5rem, 25rem);
                    -o-transform: translate(.5rem, 25rem);
                }
            }

            @include respond (phone) {
                & {
                    transform: translate(-2.5rem, 15rem);
                    -webkit-transform: translate(-2.5rem, 15rem);
                    -moz-transform: translate(-2.5rem, 15rem);
                    -ms-transform: translate(-2.5rem, 15rem);
                    -o-transform: translate(-2.5rem, 15rem);
                }
            }
            
        }
    
        & h2.slogan-2 {
            align-self: flex-end;
            transform: translate(.5rem, 12.5rem);
            -webkit-transform: translate(.5rem, 12.5rem);
            -moz-transform: translate(.5rem, 12.5rem);
            -ms-transform: translate(.5rem, 12.5rem);
            -o-transform: translate(.5rem, 12.5rem);

            @include respond (large-tab-portrait) {
                & {
                    transform: translate(.5rem, 20rem);
                    -webkit-transform: translate(.5rem, 20rem);
                    -moz-transform: translate(.5rem, 20rem);
                    -ms-transform: translate(.5rem, 20rem);
                    -o-transform: translate(.5rem, 20rem);
                }
            }

            @include respond (ipad) {
                & {
                    transform: translate(.5rem, 25rem);
                    -webkit-transform: translate(.5rem, 25rem);
                    -moz-transform: translate(.5rem, 25rem);
                    -ms-transform: translate(.5rem, 25rem);
                    -o-transform: translate(.5rem, 25rem);
                }
            }

            @include respond (phone) {
                & {
                    transform: translate(-1.5rem, 15rem);
                    -webkit-transform: translate(-1.5rem, 15rem);
                    -moz-transform: translate(-1.5rem, 15rem);
                    -ms-transform: translate(-1.5rem, 15rem);
                    -o-transform: translate(-1.5rem, 15rem);
                }
            }
        }

        & .buttons__container {
            transform: translateY(17.5rem);
            -webkit-transform: translateY(17.5rem);
            -moz-transform: translateY(17.5rem);
            -ms-transform: translateY(17.5rem);
            -o-transform: translateY(17.5rem);
            width: 100%;
            display: flex;
            flex-flow: row nowrap;
            justify-content: space-evenly;

            @include respond (large-tab-portrait) {
                & {
                    transform: translateY(25rem);
                    -webkit-transform: translateY(25rem);
                    -moz-transform: translateY(25rem);
                    -ms-transform: translateY(25rem);
                    -o-transform: translateY(25rem);
                }
            }

            @include respond (ipad) {
                & {
                    transform: translateY(30rem);
                    -webkit-transform: translateY(30rem);
                    -moz-transform: translateY(30rem);
                    -ms-transform: translateY(30rem);
                    -o-transform: translateY(30rem);
                }
            }

            @include respond (phone) {
                & {
                    width: 100%;
                    transform: translateY(17rem);
                    -webkit-transform: translateY(17rem);
                    -moz-transform: translateY(17rem);
                    -ms-transform: translateY(17rem);
                    -o-transform: translateY(17rem);
                }
            }

            & button {
                box-shadow: .7rem .7rem 1.4rem .8rem rgba(0, 0, 0, 0.25);
                animation: opacity3;
                -webkit-animation: opacity3;
                animation-duration: 3s;

            }
        }
    }    
}