@import '../../colors';
@import '../../mixins';
@import '../../keyframes';

.mobile-nav {

    &__container {
        display: none;
    }

    @include respond (hide-nav) {
        &__container {
            display: flex;
            width: 20%;
            height: 60%;
            opacity: 1;
            background: $app-boxes;
            border: 2px solid $light-gray;
            z-index: 29;
            position: fixed;
            top: 5%;
            right: 5%;
            animation-name: mobile-nav-bg;
            animation-duration: .5s;
            border-radius: 1rem;
            -webkit-border-radius: 1rem;
            -moz-border-radius: 1rem;
            -ms-border-radius: 1rem;
            -o-border-radius: 1rem;
            transform: translate(2px, -2px);
            -webkit-transform: translate(2px, -2px);
            -moz-transform: translate(2px, -2px);
            -ms-transform: translate(2px, -2px);
            -o-transform: translate(2px, -2px);

            &.leave {
                opacity: 0;
                animation-name: mobile-nav-bg-leave;
                animation-duration: .5s;
                border: 2px solid transparent;

                & .mobile-nav__list {
                    display: none;
                }
            }
        }
    }

    @include respond (tab-port) {
        &__container {
            display: flex;
            width: 30%;
            height: 40%;
            animation-name: mobile-nav-bg-tab-port;
            animation-duration: .5s;

            &.leave {
                animation-name: mobile-nav-bg-leave-tab-port;
                animation-duration: .5s;

            }
        }
    }

    @include respond (phone) {
        &__container {
            display: flex;
            width: 50%;
            height: 60%;
            animation-name: mobile-nav-bg-phone;
            animation-duration: .5s;

            &.leave {
                animation-name: mobile-nav-bg-leave-phone;
                animation-duration: .5s;
            }
        }
    }

    @include respond (large-tab-portrait) {
        &__container {
            display: flex;
            width: 40%;
            height: 60%;
            animation-name: mobile-nav-bg-large-tab-port ;
            animation-duration: .5s;

            &.leave {
                animation-name: mobile-nav-bg-leave-large-tab-port;
                animation-duration: .5s;
            }
        }
    }
    
    &__list {
        opacity: 1;
        animation-name: enter;
        animation-duration: 2s;
        list-style: none;
        display: flex;
        flex-flow: column nowrap;
        justify-content: space-evenly;
        height: 90%;
        padding-left: 0;

        @include respond (large-tab-portrait) {
            & {
                padding-left: 5rem;
            }
        }


        &-link {
            font-size: 2rem;
            font-weight: 600;
            padding-left: 2rem;

            & a {
                padding: .3rem;
                z-index: 30;
                background: transparent;
            }

            & a:link,
            & a:visited {
                text-decoration: none;
                color: $light-gray;
            }

            & a:hover,
            & a:active {
                border-bottom: 2.5px solid;
                background: $main-gradient;
                background-clip: text;
                -webkit-background-clip: text;
                color: transparent;
                cursor: pointer;
            }
        }

        &-social-media {
            display: flex;
            flex-flow: row nowrap;
            background: transparent;
            border-radius: 5rem;
            -webkit-border-radius: 5rem;
            -moz-border-radius: 5rem;
            -ms-border-radius: 5rem;
            -o-border-radius: 5rem;
            justify-content: flex-start;
            animation-name: enter;
            animation-duration: 2s;
            padding-left: 0;
            
            & a {
                width: 5rem;
                height: 5rem;
                background: transparent;
                transition: all 1s;
                -webkit-transition: all 1s;
                -moz-transition: all 1s;
                -ms-transition: all 1s;
                -o-transition: all 1s;
                backface-visibility: hidden;
                -webkit-backface-visibility: hidden;
                position: relative;
                background: none;
                padding-left: 0;
    
                & i {
                    color: $light-gray;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transition: all 1s;
                    -webkit-transition: all 1s;
                    -moz-transition: all 1s;
                    -ms-transition: all 1s;
                    -o-transition: all 1s;
                    font-size: 5rem;
                }
    
            }
    
            & a:first-of-type {
                margin-right: 2.5rem;
            }
        }
    }
}