@import '../../colors';

button {
    width: fit-content;
    padding: 0 2rem;
    height: 4.5rem;
    position: relative;
    margin-bottom: 3rem;
    border-radius: 3rem;
    -webkit-border-radius: 3rem;
    -moz-border-radius: 3rem;
    -ms-border-radius: 3rem;
    -o-border-radius: 3rem;
    font-size: 2rem;
    font-weight: 600;
    background: $app-boxes;
    color: $light-gray;
    border: 2px solid $light-gray;
    &:hover {
        cursor: pointer;
        background: $light-gray;
        color: $app-background;
    }
            
    &:focus {
        border: none;
        outline: none;
    }
}