@import '../../colors';
@import '../../mixins';

.language-picker {
    &__container {
        background: $light-gray;
        width: fit-content;
        padding: 1.2rem 1rem;
        border-radius: 5rem;
        -webkit-border-radius: 5rem;
        -moz-border-radius: 5rem;
        -ms-border-radius: 5rem;
        -o-border-radius: 5rem;
        z-index: 20;
        margin-left: auto;
        margin-right: auto;

        
        &.mobile-nav {

            display: none;

            @include respond(hide-nav) {
                display: block;
                position: fixed;
                top: 7%;
                left: 5%;
                z-index: 25;
            }
        }

        & h5 {
            margin: 0;
            color: $app-background;

            span.language-picker__english,
            span.language-picker__portugues {
                cursor: pointer;
            }

            span.language-picker__english.active,
            span.language-picker__portugues.active {
                background: $app-background;
                color: $light-gray;
                padding: .5rem;
                border-radius: 1.5rem;
                -webkit-border-radius: 1.5rem;
                -moz-border-radius: 1.5rem;
                -ms-border-radius: 1.5rem;
                -o-border-radius: 1.5rem;
            }
        }
    }
}