@import '../../colors';

.loading-spiner__container {
    width: 80%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    & .loading-spiner {
        @keyframes rotate {
            from { 
                transform: rotate(0deg); 
                -webkit-transform: rotate(0deg); -moz-transform: rotate(0deg); -ms-transform: rotate(0deg); 
                -o-transform: rotate(0deg); 
            }
            to   { 
                transform: rotate(-360deg); -webkit-transform: rotate(-360deg); -moz-transform: rotate(-360deg); -ms-transform: rotate(-360deg); -o-transform: rotate(-360deg); 
            }
        }
        
        animation: rotate 1s linear infinite;
        background: $app-background;
        border-radius: 50%;
        height: 20rem;
        width: 20rem;
        position: relative;
        
        &:before,
        &:after {
            content: '';
            position: absolute;
        }
        
        &:before {
            border-radius: 50%;
            background:
                linear-gradient(0deg,   hsla(0, 0%, 100%, 1  ) 50%, hsla(0, 0%, 100%, 0.9) 100%)   0%   0%,
                linear-gradient(90deg,  hsla(0, 0%, 100%, 0.9)  0%, hsla(0, 0%, 100%, 0.6) 100%) 100%   0%,
                linear-gradient(180deg, hsla(0, 0%, 100%, 0.6)  0%, hsla(0, 0%, 100%, 0.3) 100%) 100% 100%,
                linear-gradient(360deg, hsla(0, 0%, 100%, 0.3)  0%, hsla(0, 0%, 100%, 0  ) 100%)   0% 100%
            ;
            background-repeat: no-repeat;
            background-size: 50% 50%;
            top: -1px;
            bottom: -1px;
            left: -1px;
            right: -1px;
        }
        
        &:after {
            background: $app-background;
            border-radius: 50%;
            top: 3%;
            bottom: 3%;
            left: 3%;
            right: 3%;
        }
    }
}


