@import '../../keyframes';
@import '../../colors';

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100%;
    opacity: 0.7;
    background: #000;
    z-index: 28;
    animation-name: overlay;
    animation-duration: .5s;
    
    &.leave {
        animation-name: overlay-leave;
        animation-duration: .5s;
    }
}